
const state = {
    moneys: [
        { value: 10 },
        { value: 20 },
        { value: 50 },
        { value: 100 },
        { value: 200 },
        { value: 500 },
        { value: 1000 },
    ],
    payment: {
        money: 0,
        product: {

        }
    },
    result: "Vásárolj valamit"
}

const getters = {
    moneyValues: (state) => state.moneys,
    getRes: (state) => state.result,
    getMoney: (state) => state.payment.money,
}

const actions = {
    setValues({ commit }, value) {
        commit("setMoney", value)
    },
    setProduct({ commit }, product) {
        commit("setProduct", product)
    },
    pay({ commit }, product) {
        let money = state.payment.money;
        //let product = state.payment.product;
        let diff = money - product.Price
        let res;
        if (diff >= 0) {
            res = "A " + product.Name + " sikeresen megvásárolva a visszajáró: " + diff
            commit("moneyReset")
        }
        else {
            res = "Dobj még be "+Math.abs(diff)+"ft-ot"
        }
        commit("setPayRes", res)
       
        
    }
}

const mutations = {
    setMoney: (state, value) => (state.payment.money += value),
    moneyReset: (state) => (state.payment.money = 0),
    setProduct: (state, product) => (state.payment.product = product),
    setPayRes: (state, res) => (state.result = res)
}

export default {
    state,
    getters,
    actions,
    mutations
}
