<template>
    <div v-on:click="getValue(value)" >
         <div v-bind:class="currentClass" >{{ value+"ft" }}</div>
    </div>
</template>

<script>
//import { mapActions } from 'vuex'
    export default {
        name: 'Money',
        props: {
            value: Number,
        },
        data() {
            let currentClass = Number(this.value) > 200 ? "paper-money" : "coin";
            return {
                currentClass: currentClass,
                moneyValue: 0
            }
        },
       methods: {
           getValue(data){
                 this.$store.dispatch("setValues",data)
           }
       },
       
    }
</script>

