<template>
    <div>
        <div class="product" v-on:click="getProduct()">
            <span>
                <p> {{name}} </p>
                <p> {{price}} </p>
            </span>
        </div>
    </div>
</template>

<script>
import {mapGetters} from "vuex"
    export default {
        name: 'Product',
        props: {
            name: String,
            price: Number
        },
        computed: {
             ...mapGetters({result:"getResult"})
        },
        methods: {
            getProduct:function(){
                let product = {Name:this.name,Price:this.price}
                //this.$emit("getProduct","product")
                this.$store.dispatch("pay",product)
            },
            
        },


    }
</script>

